const RegUrlRootStart = /^\.{0,1}\//;
const RegUrlRootEnd = /\/$/;

class Database {

  constructor (data) {
    this.Data = data || {};
    this.Hash = {};
  }

  formatUrl(url) {
    return url.replace(RegUrlRootStart, '').replace(RegUrlRootEnd, '');
  }

  matchUrl(keyUrl, updateUrl) {
    let keyStr = this.formatUrl(keyUrl),
      updateStr = this.formatUrl(updateUrl);
    return keyStr.indexOf(updateStr) !== -1 || updateStr.indexOf(keyStr) !== -1;
  }

  _urlToArray(url) {
    let arr = url.split('/');
    if (arr[0] === '') {
      arr.shift();
    }
    return arr;
  }

  val(url, data) {
    const _url = this.formatUrl(url),
      override = arguments.length > 1,
      pathArray = this._urlToArray(_url);
    // root
    if (pathArray.length === 0) {
      if (override) {
        this.Data = data;
      }
      return this.Data;
    }
    // length > 1
    const l = pathArray.length,
      last = l - 1;
    let i = 0,
      pName,
      p = this.Data;
    for (; i < l; i++) {
      pName = pathArray[i];
      if (i === last) {
        if (override) {
          p[pName] = data;
        }
        return p[pName];
      } else if (typeof p[pName] !== 'object' || p[pName] == null) {
        if (override) {
          p[pName] = {};
        } else {
          return undefined;
        }
      }
      p = p[pName];
    }
  }

}

/*
  './arc/index',
  'arc/index'
  '/arc/index'
*/

// Database.prototype.ShapeName = 'arc';

export default Database;
