import * as d3 from "d3";
import core from '../utils/core';
import Database from '../utils/database';
import Preload from '../preload/index';
import Axis from '../axis/index';

const HASHROOT = '$ROOTNODE';
const REGHASHROOT = /^\$ROOTNODE/;

class Board {
  constructor (query, config, data) {
    this.Con = d3.select(query);
    if (process && process.env && process.env.NODE_ENV === 'test') {
      this.Svg = this.Con.append('svg');
    } else {
      this.Svg = this.Con.append(function () {
        // dynamic add svg because of browser - -
        return document.createElementNS("http://www.w3.org/2000/svg", "svg");
      });
    }
    // this.Defs = this.Svg.append('defs');
    this.Shapes = {};
    this.PreloadStack = {};
    this.Database = new Database(data);
    this.UrlHash = {};
    this.modify(Object.assign({}, this.defaultConfig.modify, config));
  }

  static _registerShape(shape) {
    let name = core.lowerCaseInital(shape.prototype.ShapeName);
    this.prototype[name] = function () {
      const arr = core.toArray(arguments);
      arr.unshift(shape);
      return this.add.apply(this, arr);
    };
  }

  static registerShape() {
    core.extendArguments(arguments, this._registerShape, this);
  }

  static registerShapesObj(shapes) {
    for (let n in shapes) {
      this._registerShape(shapes[n]);
    }
  }

  createDefs() {
    if (this.Defs === undefined) {
      this.Defs = this.Svg.append('defs');
    }
  }

  preload(dataUpdate, shapeLoader) {
    this.createDefs();
    let preLoader = new Preload(dataUpdate, shapeLoader || this.Shapes, this);
    this.PreloadStack[preLoader.Guid] = preLoader;
    return preLoader;
  }

  cancelPreload() {
    for (let id in this.PreloadStack) {
      this.PreloadStack[id].detach();
    }
  }

  // data({}) data('p',{}) data('p')
  // [url], data
  data() {
    let arr = core.toArray(arguments);
    let url = typeof arr[0] === 'string' ? arr.shift() : './';
    if (arr.length > 0) {
      return this.Database.val(url, arr[0]);
    } else {
      return this.Database.val(url);
    }
  }

  modify(config) {
    core.modifySelection(this.Svg, config);
  }

  call() {
    // call is override by D3.js
    this.Svg.call(arguments);
  }

  clear() {

  }

  axis(name, config) {
    let axis = new Axis(name, config);
    axis.attach(this.Svg);
    return axis;
  }

  zoom(scale) {
    scale = scale || [0.1, 2];
    let zoom = d3.zoom();
    zoom.scaleExtent(scale);
    zoom.on('zoom', () => {
      this._handleZoom(d3.event.transform);
    });
    this.Svg.call(zoom);
    return zoom;
  }

  _handleZoom(transform) {
    this.Svg.attr("transform", "translate(" +
      transform.x + ',' +
      transform.y + ")scale(" +
      transform.k + ")");
  }

  resetZoom() {
    this._handleZoom({
      x: 1,
      y: 1,
      k: 1
    });
  }

  // boolen - null
  // boolen - data
  // boolen - url data
  render() {
    // update database
    const arr = core.toArray(arguments);
    let ifUpdateData;
    if (arr.length > 0 && typeof arr[0] === 'boolean') {
      ifUpdateData = arr.shift();
    } else {
      ifUpdateData = arr.length > 0;
    }

    // update data
    if (ifUpdateData) {
      this.data.apply(this, arr);
    }
    const url = this.Database.formatUrl(arr.length > 1 ? arr.shift() : './'),
      ifUpdateAll = url === '',
      shapeList = ifUpdateAll ? this.Shapes : this.hash(url);
    let shape;
    if (ifUpdateData) {
      for (let n in shapeList) {
        shape = shapeList[n];
        shape.render.call(shape, this.data(shape.url()));
      }
    } else {
      for (let n in shapeList) {
        shapeList[n].render();
      }
    }
  }

  // config url data
  transition() {
    const arr = core.toArray(arguments),
      config = arr.shift(),
      ifUpdateData = arr.length > 0;

    if (ifUpdateData) {
      this.data.apply(this, arr);
    }

    const url = this.Database.formatUrl(arr.length > 1 ? arr.shift() : './'),
      shapeList = url === '' ? this.Shapes : this.hash(url);
    let shape;
    if (ifUpdateData) {
      for (let n in shapeList) {
        shape = shapeList[n];
        shape.transition.apply(shape, [config, this.data(shape.url())]);
      }
    } else {
      for (let n in shapeList) {
        shapeList[n].transition(config);
      }
    }

  }

  hashString(str, ifReverse) {
    if (ifReverse) {
      return str.replace(REGHASHROOT, '');
    } else {
      return str === '' ? HASHROOT + str : str;
    }
  }

  hash(url) {
    const arr = {};
    const hash = this.UrlHash;
    let keyUrl,
      hashObj;
    for (let key in hash) {
      keyUrl = this.hashString(key, true);
      if (this.Database.matchUrl(keyUrl, url)) {
        hashObj = hash[key];
        for (let guid in hashObj) {
          arr[guid] = hashObj[guid];
        }
      }
    }
    return arr;
  }

  // _constructor, dataUrl, config
  add() {
    const arr = core.toArray(arguments),
      _constructor = arr.shift(),
      dataUrl = typeof arr[0] === 'string' ? arr.shift() : '',
      url = this.Database.formatUrl(dataUrl),
      hashKey = this.hashString(url),
      data = this.Database.val(url),
      config = arr.length > 0 ? arr[0] : undefined,
      shape = new _constructor(data, config);
    shape.url(url);
    this.Shapes[shape.Guid] = shape;
    if (this.UrlHash[hashKey] === undefined) {
      this.UrlHash[hashKey] = {};
    }
    this.UrlHash[hashKey][shape.Guid] = shape;
    shape.attach(this.Svg);
    return shape;
  }

  remove(shape) {
    const guid = typeof shape === 'string' ? shape : shape.Guid;
    const hashKey = this.hashKey(shape.url());
    if (this.UrlHash[hashKey]) {
      delete this.UrlHash[hashKey][guid];
    }
    delete this.Shapes[guid];
  }
}

Board.prototype.defaultConfig = {
  modify: {
    classed: {
      'md-chart-board': true
    }
  }
};

export default Board;
